import * as VueApolloComposable from "@vue/apollo-composable";
import { ref, reactive } from "vue";
import { GetRunningInfoByIdDocument } from "./queries"; //   
import { GetRunningInfoByIdQueryVariables, GetRunningInfoByIdQuery } from "@/gql"; //  

//      
export function useGetRunningInfoByIdQuery(
    variables: GetRunningInfoByIdQueryVariables, //   
    options: VueApolloComposable.UseQueryOptions<GetRunningInfoByIdQuery, GetRunningInfoByIdQueryVariables> = {} //   
) {
    // variables options ref   
    const reactiveVariables = ref(variables);
    const reactiveOptions = reactive(options);

    return VueApolloComposable.useQuery(
        GetRunningInfoByIdDocument,  // GraphQL 
        reactiveVariables,          //  
        reactiveOptions            //  
    );
}
