<template>
  <div>
    <canvas id="abnormalChart" width="400" height="200" class="abnormalChart"></canvas>

    <!--    -->
    <ul style="list-style: none; padding: 0;">
      <li v-for="(item, idx) in autoNomarlCheck" :key="idx" class="normalCheckList">
        <span style="font-weight: bold; color: #e63946;">{{ item.classNm }}</span>  :
        <span style="color: #e76f51;">{{ item.value }}</span>
        <small style="color: #6c757d;">( : {{ item.normalRangeFirst }} - {{ item.normalRangeLast }})</small>
      </li>
    </ul>
  </div>
</template>

<script setup lang="ts">
import { defineProps, onMounted } from "vue";
import { Chart, BarController, BarElement, PointElement, CategoryScale, LinearScale, Tooltip, Legend, Title, LineController, LineElement } from "chart.js";

// Chart.js   
Chart.register(BarController, BarElement, PointElement, CategoryScale, LinearScale, Tooltip, Legend, Title, LineController, LineElement);

const props = defineProps(['autoNomarlCheck']);

onMounted(() => {
  //  
  const labels = props.autoNomarlCheck.map(item => item.classNm);
  const normalRangeData = props.autoNomarlCheck.map(item => ({
    x: item.classNm,
    y: [item.normalRangeFirst, item.normalRangeLast] // Floating Bars  
  }));
  const dataValues = props.autoNomarlCheck.map(item => item.value);

  const data = {
    labels: labels,
    datasets: [
      {
        label: ' ',
        data: normalRangeData,
        backgroundColor: '#007bff',
        borderColor: '#007bff',
        borderWidth: 1,
        barThickness: 30, //   
      },
      {
        label: ' ',
        data: dataValues,
        type: 'line',
        borderColor: '#1DDDE0',
        backgroundColor: '#1DDDE0',
        borderWidth: 0,
        pointRadius: 3, //   
        pointBackgroundColor: '#1DDDE0',
      },
    ],
  };

  //   
  const config = {
    type: 'bar',
    data: data,
    options: {
      responsive: true,
      plugins: {
        legend: {
          position: 'top',
        },
      },
      scales: {
        x: {
          grid: {
            display: false, // x  
          },
          ticks: {
            color: '#333',
            font: {
              size: 12,
            },
          },
        },
        y: {
          beginAtZero: true,
          grid: {
            color: 'rgba(200, 200, 200, 0.3)', // y   
            borderDash: [5, 5], //  
          },
          ticks: {
            color: '#333',
            font: {
              size: 12,
            },
          },
        },
      },
    },
  };

  //  
  new Chart(document.getElementById('abnormalChart') as HTMLCanvasElement, config);
});

</script>
