<template>
  <div v-if="visible" class="toast-notification" :class="[position, messageType]">
    <p>{{ message }}</p>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, watch, defineProps } from 'vue';

// Props : , ,  
const props = defineProps({
  message: {
    type: String,
    required: true,
  },
  duration: {
    type: Number,
    default: 3000, //   ( 3)
  },
  position: {
    type: String,
    default: 'bottom-right', //  :  
  },
  messageType: {
    type: String,
  }
});

const visible = ref(true);

onMounted(() => {
  // duration  visible  false   
  setTimeout(() => {
    visible.value = false;
  }, props.duration);
});

watch(props, (newProps) => {
  // props      
  visible.value = true;
  setTimeout(() => {
    visible.value = false;
  }, newProps.duration);
});
</script>
